import React, { useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import { Container, Button, Typography } from '@mui/material';
import axios from 'axios';

const ContactDetailsPage = () => {
    const { id } = useParams();
    const [contact, setContact] = useState(null);

    useEffect(() => {
        axios.get(`http://laravel-test.test/api/contacts/${id}`)
            .then(response => setContact(response.data.data))
            .catch(error => console.log('Error fetching contact details:', error));
    }, [id]);

    if (!contact) return <p>Loading...</p>;

    return (
        <Container>
            <Typography variant="h4" gutterBottom>Contact Details</Typography>
            <Typography variant="h6">Name: {contact.name}</Typography>
            <Typography variant="body1">Contact: {contact.contact}</Typography>
            <Typography variant="body1">Email: {contact.email}</Typography>

            <Button component={Link} to={`/contacts/${id}/edit`} variant="outlined">Edit</Button>
            <Button color="error" variant="contained">Delete</Button>
        </Container>
    );
};

export default ContactDetailsPage;
