import React from 'react';
import { useRoutes } from 'react-router-dom'; // Importa useRoutes

import ContactsIndex from './pages/ContactsIndex'; // Página de lista de contatos
import AddContactPage from './pages/AddContactPage'; // Página de adicionar contato
import ContactDetailsPage from './pages/ContactDetailsPage'; // Página de detalhes do contato

// Defina suas rotas
const routes = [
  {
    path: "/",
    element: <ContactsIndex />, // Página inicial com lista de contatos
  },
  {
    path: "/contacts/add", // Rota para adicionar um novo contato
    element: <AddContactPage />,
  },
  {
    path: "/contact/:id", // Rota para mostrar os detalhes de um contato específico
    element: <ContactDetailsPage />,
  },
];

function App() {
  const routing = useRoutes(routes); // Define as rotas

  return routing; // Renderiza as rotas
}

export default App;
