import React, { useState } from 'react';
import { TextField, Button, Container, Typography } from '@mui/material';
import axios from 'axios';
// import { useHistory } from 'react-router-dom';

const AddContactPage = () => {
    const [formData, setFormData] = useState({
        name: '',
        contact: '',
        email: '',
    });
    const [errors, setErrors] = useState([]);
    // const history = useHistory();

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData(prevData => ({
            ...prevData,
            [name]: value
        }));
    };

    const handleSubmit = (e) => {
        e.preventDefault();

        // axios.post('http://laravel-test.test/api/contacts', formData)
        //     .then(response => {
        //         history.push('/');
        //     })
        //     .catch(error => {
        //         if (error.response && error.response.data.errors) {
        //             setErrors(error.response.data.errors);
        //         }
        //     });
    };

    return (
        <Container>
            <Typography variant="h4" gutterBottom>Add Contact</Typography>
            <form onSubmit={handleSubmit}>
                <TextField
                    label="Name"
                    name="name"
                    fullWidth
                    value={formData.name}
                    onChange={handleInputChange}
                    error={errors.name ? true : false}
                    helperText={errors.name && errors.name[0]}
                    margin="normal"
                />
                <TextField
                    label="Contact"
                    name="contact"
                    fullWidth
                    value={formData.contact}
                    onChange={handleInputChange}
                    error={errors.contact ? true : false}
                    helperText={errors.contact && errors.contact[0]}
                    margin="normal"
                />
                <TextField
                    label="Email"
                    name="email"
                    fullWidth
                    value={formData.email}
                    onChange={handleInputChange}
                    error={errors.email ? true : false}
                    helperText={errors.email && errors.email[0]}
                    margin="normal"
                />
                <Button type="submit" variant="contained" color="primary" fullWidth>Add</Button>
            </form>
        </Container>
    );
};

export default AddContactPage;
